import React, { Component } from 'react';
import Table from '../../src';
import filterColumn from '../../src/lib/filterColumn';
import Icon from "bee-icon";

const columns21 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    width: 100
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: 100
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: 200,
    sorter: (a, b) => a.c - b.c
  },
  {
    title: "",
    dataIndex: "d",
    key: "d"
  }
];

const data21 = [
  { a: "", b: "", c: 30,d:'', key: "2" },
  { a: "", b: "", c: 41,d:'', key: "1" },
  { a: "", b: "", c: 25,d:'', key: "3" }
];

const FilterColumnTable = filterColumn(Table);

const defaultProps21 = {
  prefixCls: "bee-table"
};

class Demo21 extends Component {
  constructor(props) {
    super(props);
  }
 
  render() {
    
    return <FilterColumnTable columns={columns21} data={data21} />;
  }
}
Demo21.defaultProps = defaultProps21;


export default Demo21;


